  margin-bottom: 20px;
  border: 1px solid #fff641;
  background: #2a2a21;
  color: #fff641; }
  input:focus, input:active, textarea:focus, textarea:active {
    border-color: #fff; }

label {
  padding: 0;
  margin-bottom: 5px;
  display: block;
  font-weight: 700; }

button, input, optgroup, select, textarea {
  font-family: sans-serif; }

button,
.button,
input[type="submit"] {
  padding: 15px 25px;
  margin: 0 0 10px 0;
  line-height: 1.3;
  box-shadow: none;
  border: none;
  background-color: #fff;
  color: #2a2a21;
  cursor: pointer; }
  button p,
  .button p,
  input[type="submit"] p {
    margin: 0; }
  button:hover,
  .button:hover,
  input[type="submit"]:hover {
    background-color: #aaa; }

a.button {
  display: block;
  text-align: center; }

@keyframes slideIn {
  0% {
    transform: translateX(-10px); }
  50% {
    transform: translateX(10px); }
  100% {
    transform: translateX(0px); } }

.flash {
  background: white;
  margin: 20px 0 0;
  padding: 20px;
  position: relative;
  z-index: 1;
  border-radius: 3px;
  display: flex;
  animation: slideIn forwards .1s ease-in-out 2;
  animation-timing-function: cubic-bezier(0.01, 1.68, 0.58, 1); }
  .flash + .flash {
    animation-delay: 0.55s; }
    .flash + .flash + .flash {
      animation-delay: 0.6s; }
  .flash-text {
    flex: 1; }
  .flash-remove {
    background: none;
    border: 0; }
    .flash-remove:hover {
      color: #fff641; }
  .flash button {
    padding: 0;
    color: #827cd2;
    background-color: #fff;
    margin: 0; }
    .flash button:hover {
      background-color: #fff;
      color: #2a2a21; }
  .flash:after {
    content: '';
    display: block;
    background: white;
    position: absolute;
    width: calc(100% - 16px);
    height: calc(100% - 16px);
    top: 8px;
    left: 8px;
    z-index: -1;
    border-radius: 3px; }
  .flash-success {
    background: green; }
  .flash-error {
    background: red; }
  .flash-info {
    background: yellow; }
  .flash p {
    margin: 0; }

.nav {
  display: grid;
  list-style: none;
  margin: 0;
  padding: 0;
  grid-template-columns: 1fr 1fr;
  background: transparent; }
  .nav-section {
    display: grid;
    grid-gap: 25px;
    grid-template-columns: auto auto;
    align-items: stretch; }
  .nav #logo {
    justify-self: start; }
  .nav #user {
    justify-self: end; }
  .nav a {
    display: grid;
    align-items: center;
    padding: 5px 0; }
  .nav h2 {
    padding: 0;
    margin: 0;
    font-size: 50px;
    line-height: 1;
    text-transform: none; }
  .nav .user-form {
    display: none;
    background: #fff;
    padding: 10px;
    border-radius: 10px; }
  .nav .visible-form .user-form {
    display: block; }

.hide {
  text-indent: -999999px;
  font-size: 0; }

.avatar {
  min-width: 50px;
  height: 50px;
  border-radius: 50%;
